* {
  box-sizing: border-box; }

body {
  font-family: Helvetica;
  font-size: 16px; }

.modal-overlay {
  position: fixed;
  z-index: 9;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3rem;
  transition-timing-function: ease-out;
  transition-duration: 0;
  will-change: transform, opacity, padding;
  touch-action: none; }
  @media screen and (max-width: 768px) {
    .modal-overlay {
      padding: 0; } }

.modal-shadow {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); }

.cross {
  position: absolute;
  z-index: 9;
  top: 0;
  right: 0;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  transition: 0.1s ease-out;
  text-align: center;
  opacity: 0.6;
  color: #fff;
  font-size: 3rem;
  will-change: opacity, top, right, width, height, background-color, padding, border-radius, color; }
  @media screen and (max-width: 768px) {
    .cross {
      top: 0.5rem;
      right: 0.5rem;
      width: 2rem;
      height: 2rem;
      padding: 0.5rem;
      color: #111;
      border-radius: 50%;
      background-color: #fff;
      font-size: 1rem; } }
  .cross::after {
    content: '\2715'; }
  .cross:hover {
    opacity: 1; }

.modal-body {
  width: 100%;
  height: 100%;
  background-color: #111; }

.gallery {
  display: flex;
  overflow-y: auto;
  flex-direction: row;
  flex-wrap: wrap;
  max-height: 100%;
  padding-bottom: 5px;
  padding-left: 5px; }
  .gallery::after {
    flex-grow: 9999;
    content: ''; }
  @media screen and (max-width: 768px) and (orientation: landscape) {
    .gallery {
      flex-wrap: nowrap;
      max-width: 100%;
      height: 100vh; } }

.image-tile {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
  height: 200px;
  height: calc(150px + 5vh);
  padding: 5px;
  padding-bottom: 0;
  padding-left: 0;
  cursor: pointer;
  outline: none;
  appearance: none; }
  .image-tile::after {
    position: absolute;
    top: 5px;
    right: 5px;
    bottom: 0;
    left: 0;
    content: '';
    transition: 0.1s ease;
    opacity: 0;
    background-color: #000;
    will-change: opacity; }
  .image-tile:hover::after, .image-tile:focus::after {
    visibility: visible;
    opacity: 0.3; }
  .image-tile__image {
    display: block;
    flex-grow: 1;
    min-width: 100%;
    max-width: 100%;
    height: inherit;
    object-fit: cover; }
  @media screen and (max-width: 480px) and (orientation: portrait) {
    .image-tile {
      flex-basis: 100%;
      height: auto; } }
  @media screen and (max-width: 768px) and (orientation: landscape) {
    .image-tile {
      flex-shrink: 0;
      height: inherit;
      max-height: 100%; } }

.preview {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%; }
  .preview__image {
    max-width: 100%;
    max-height: 100%; }



